<template>
  <v-container>
    <div class="submit-form mt-3 mx-auto">
      <v-row>
        <v-col class="text-center" cols="12" md="8">
          <v-row class="pa-5">
            <h2>ADD BRANCH</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/branches"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >BRANCH LIST</v-btn
          >
        </v-col>
      </v-row>

      <!-- <div v-if="!submitted"> -->
      <div>
        <v-form ref="form" lazy-validation>
          <v-text-field
            v-model="branch.name"
            :rules="[(v) => !!v || 'Branch Name is required']"
            label="Branch Name"
            required
          ></v-text-field>

          <v-text-field
            v-model="branch.brCode"
            :rules="[(v) => !!v || 'Branch Code is required']"
            label="Branch Code"
            required
          ></v-text-field>
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="saveBranch"
          >Submit</v-btn
        >
      </div>

      <!-- <div v-else>
        <v-card class="mx-auto">
          <v-card-title> Submitted successfully! </v-card-title>

          <v-card-subtitle>
            Click the button to add new Branch.
          </v-card-subtitle>

          <v-card-actions>
            <v-btn color="success" @click="newBranch">Add</v-btn>
          </v-card-actions>
        </v-card>
      </div> -->
    </div>

    <!-- <v-snackbar top v-model="snackbar" :timeout="timeout">
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar> -->
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";

export default {
  name: "add-branch",
  data() {
    return {
      // snackbar: false,
      // text: "",
      // timeout: 2000,
      branch: {
        id: null,
        name: "",
        brCode: "",
      },
      // submitted: false,
    };
  },

  computed: {
    isAddButtonDisabled() {
      return !(this.branch.name && this.branch.brCode);
      // means u have neither branch name and branch code
    },
  },

  methods: {
    saveBranch() {
      var data = {
        name: this.branch.name,
        brCode: this.branch.brCode,
      };

      BranchDataService.create(data)
        .then((response) => {
          this.branch.id = response.data.id;
          // console.log(response.data);
          // this.submitted = true;
          // this.snackbar = true;
          // this.text =
          //   (response && response.data && response.data.message) || toString();
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "branches" });
        })
        .catch((e) => {
          // console.log(e);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
          // this.text =
          //   (e.response && e.response.data && e.response.data.message) ||
          //   e.message ||
          //   e.toString();
          // this.snackbar = true;
        });
    },

    // newBranch() {
    //   this.submitted = false;
    //   this.branch = {};
    // },
  },
};
</script>
